const gameScene = new Phaser.Class({
  Extends: Phaser.Scene,

  initialize: function GameScene() {
    Phaser.Scene.call(this, { key: 'gameScene' });
  },

  preload() {
    // –ó–∞–≥—Ä—É–∑–∫–∞ –∞—Å—Å–µ—Ç–æ–≤ –∏–≥—Ä—ã
    this.load.image('backgroundGame', 'https://i.postimg.cc/ncCJCFZ6/wallpaperbetter-com-3840x2160-1.jpg');
    this.load.spritesheet('tHex', 'https://i.postimg.cc/NM746MZ3/big-sprites-copy.png', {
      frameWidth: 243,
      frameHeight: 370,
    });
    this.load.image('ground', 'https://i.postimg.cc/8c9BWznw/ground.png');
    this.load.image('obstacle1', 'https://i.postimg.cc/ZRfh2H7N/car.png');
    this.load.image('obstacle2', 'https://i.postimg.cc/zXH12nnL/container.png');
    this.load.image('obstacle3', 'https://i.postimg.cc/Zn9XgSyk/mahina.png');
    this.load.image('obstacle4', 'https://i.postimg.cc/X7krqSzc/trash-ready.png');
  },

  create() {
    this.anims.create({
      key: 'jump',
      frames: this.anims.generateFrameNumbers('tHex', { start: 3, end: 3 }),
      frameRate: 10,
    });
    this.anims.create({
      key: 'run',
      frames: this.anims.generateFrameNumbers('tHex', { start: 0, end: 1 }),
      frameRate: 10,
      repeat: -1,
    });
    this.anims.create({
      key: 'idle',
      frames: this.anims.generateFrameNumbers('tHex', { start: 2, end: 2 }),
      frameRate: 10,
    });

    const { width, height } = this.sys.game.config;

    // –ó–∞–¥–Ω–∏–π —Ñ–æ–Ω –∏–≥—Ä—ã
    this.backgroundImage = this.add
      .tileSprite(0, 0, this.sys.game.config.width, this.sys.game.config.height, 'backgroundGame')
      .setOrigin(0, 0)
      .setScrollFactor(0)
      .setScale(3160 / width, 2140 / height);

    this.tHex = this.physics.add.sprite(1080, this.sys.game.config.height - 200, 'tHex'); // –ø–µ—Ä—Å–æ–Ω–∞–∂

    this.grounds = this.physics.add.staticGroup();
    for (let i = 0; i < 3; i += 1) {
      const ground = this.grounds.create(1080 + i * 1080, this.sys.game.config.height, 'ground');
      this.physics.add.collider(this.tHex, ground);
    }

    this.tHex.setGravityY(800); // –≥—Ä–∞–≤–∏—Ç–∞—Ü–∏—è –ø–æ –≤–µ—Ä—Ç–∏–∫–∞–ª–∏
    this.tHex.setScale(0.5); // –º–∞—Å—à—Ç–∞–±–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞

    const cursors = this.input.keyboard.createCursorKeys(); // –æ–±—ä–µ–∫—Ç, —Å–æ–¥–µ—Ä–∂–∞—â–∏–π –∫–ª–∞–≤–∏—à–∏ —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è
    const jump = () => {
      if (this.tHex.body.touching.down) {
        this.tHex.setVelocityY(-500);
        this.tHex.setVelocityX(300);
        this.isRunning = true;
        this.score += 10; // –Ω–∞—á–∏—Å–ª–µ–Ω–∏–µ 10 –±–∞–ª–ª–æ–≤ –∑–∞ –ø—Ä—ã–∂–æ–∫
        this.scoreText.setText(`Score: ${this.score}`); // –æ–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Ç–µ–∫—Å—Ç–∞ —Å—á–µ—Ç–∞
        this.tHex.anims.play('jump'); // —Ç–µ–∫—Å—Ç—É—Ä–∞ –ø—Ä—ã–∂–∫–∞
      }
    };

    cursors.up.on('down', jump); // –ø—Ä—ã–∂–æ–∫ –ø–æ —Å—Ç—Ä–µ–ª–∫–µ –≤–≤–µ—Ä—Ö
    cursors.space.on('down', jump); // –ø—Ä—ã–∂–æ–∫ –ø–æ –ø—Ä–æ–±–µ–ª—É
    this.input.on('pointerdown', jump); // –ø—Ä—ã–∂–æ–∫ –ø–æ –Ω–∞–∂–∞—Ç–∏—é —ç–∫—Ä–∞–Ω–∞

    this.speed = 5; // —Å–∫–æ—Ä–æ—Å—Ç—å –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏—è –ø–µ—Ä—Å–æ–Ω–∞–∂–∞
    this.isRunning = false;

    this.cameras.main.startFollow(this.tHex, true, 0.5, 0.5, 0, 150);

    this.score = 0; // –ø–µ—Ä–µ–º–µ–Ω–Ω–∞—è –¥–ª—è —Ö—Ä–∞–Ω–µ–Ω–∏—è —Ç–µ–∫—É—â–µ–≥–æ —Å—á–µ—Ç–∞
    this.scoreText = this.add
      .text(16, 16, 'Score: 0', { fontSize: '32px', fill: '#fff', fontFamily: 'MinecraftiaRegular' }) // —Ç–µ–∫—Å—Ç —Å—á–µ—Ç–∞
      .setScrollFactor(0);

    this.obstacles = this.physics.add.group({ immovable: true }); // –ü—Ä–µ–ø—è—Ç—Å—Ç–≤–∏–µ –∏ –∏—Ö –ª–æ–≥–∏–∫–∞
    this.time.addEvent({
      delay: 3000, // –≤—Ä–µ–º—è –≤ –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥–∞—Ö –º–µ–∂–¥—É –∫–∞–∂–¥—ã–º —Å–ø–∞–≤–Ω–æ–º
      callback: this.spawnObstacle,
      callbackScope: this,
      loop: true,
    });
    // —Ñ—É–Ω–∫—Ü–∏—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è, –ø–µ—Ä–µ–º–µ—â–∞—é—â–∞—è –ø–µ—Ä—Å–æ–Ω–∞–∂–∞ –≤–ø–µ—Ä–µ–¥
    this.update = () => {
      if (this.isRunning) {
        this.tHex.x += this.speed;
        this.backgroundImage.tilePositionX = this.cameras.main.scrollX * 0.3; // –º–µ–¥–ª–µ–Ω–Ω–æ–µ –¥–≤–∏–∂–µ–Ω–∏–µ
      }
      if (this.tHex.body.touching.down && this.tHex.anims.currentAnim && this.tHex.anims.currentAnim.key === 'jump') {
        this.tHex.anims.play('run'); // –í–æ—Å–ø—Ä–æ–∏–∑–≤–µ–¥–µ–Ω–∏–µ –æ–±—ã—á–Ω–æ–π –∞–Ω–∏–º–∞—Ü–∏–∏ –ø–µ—Ä—Å–æ–Ω–∞–∂–∞
      }
      if (this.tHex.x > this.grounds.getChildren()[0].x + 1080) {
        // –£–¥–∞–ª–∏—Ç—å —Å—Ç–∞—Ä—É—é –∑–µ–º–ª—é
        this.grounds.getChildren()[0].destroy();

        // –î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤—É—é –∑–µ–º–ª—é
        const newGround = this.grounds.create(this.grounds.getChildren()[this.grounds.getChildren().length - 1].x + 1080, this.sys.game.config.height, 'ground');
        this.physics.add.collider(this.tHex, newGround);
      }
    };
  },
  spawnObstacle() {
    const obstacleNumber = Phaser.Math.Between(1, 4);
    const randomX = Phaser.Math.Between(this.tHex.x + Phaser.Math.Between(1000, 1500), this.tHex.x + 3000);
    this.physics.world.setBounds(0, 0, randomX + 2000, this.sys.game.config.height);
    const obstacle = this.obstacles.create(randomX, this.sys.game.config.height - 100, `obstacle${obstacleNumber}`).setScale(0.2);
    obstacle.setDepth(1);
    obstacle.setCollideWorldBounds(true);
    obstacle.setVelocityX(-this.speed);
    this.physics.add.collider(this.tHex, obstacle, () => {
      this.scene.start('gameOver');
    }, null, this);
    this.grounds.children.iterate((child) => {
      this.physics.add.collider(obstacle, child);
    });
  },
});
export { gameScene };
